import React, {Component} from 'react';
import FAQ_illus from '../videos/FAQs.mp4';

class FAQ extends Component {
    render(){
        return(
            <div class="faq_widget">
                <h3><i class="fi-rr-shield-interrogation before_icon"></i> Frequently Asked Questions (FAQ)  </h3>
          
          <div className="QAWidget_main">
              <div class="QA_left">
                <div class="QAWidget">
                    <h4><span>Q.</span> What is your main aim of creating NudityDetectionAlgo?</h4>
                    <p> <span> A. </span> To reduce or eliminate the rate at which Nudity is rampant in the internet and
                    to make the internet a safe place for Kids.</p>
                </div>

                <div class="QAWidget">
                    <h4><span>Q.</span> How does NudityDetectionAlgo work?</h4>
                    <p> <span> A. </span> NudityDetectionAlgo works by detecting Nude content
                    and preventing them from being uploaded to the Internet. </p>
                </div>

                <div class="QAWidget">
                    <h4><span>Q.</span> What is your main aim of creating this?</h4>
                    <p> <span> A. </span> To prevent the rate at which Nudity is rampant in the social media.</p>
                </div>
             </div>

          
                    <video className="FAQ_illus" autoplay>
                        <source src={FAQ_illus} />
                    </video>

             </div>

            </div>
        );
    }
}

export default FAQ;