import React, {Component} from 'react';
import Neural from '../images/neural.svg';
import API from '../images/api.svg';
import Nude from '../images/woman.svg';

class Features extends Component{
    render(){
        return(
            <div class="feature_widget">

                    <h3><i class="fi-rr-apps before_icon"></i> Features  </h3>

                    <div class="about_page_meta_bar">

                        <div class="about_page_meta_child">
                            <img src={Neural}className="about_page_icon" />
                            <h3> Super trained and 99+1% Working Algorithm </h3>
                            <p> NudityDetectionAlgo is a Machine learning model which was trained with profuse dataset 
                                and reads uploaded content to check if Nudity is present. This Model Flags
                                and alerts User about the contents (Images / Videos) they upload and how dangerous it might be.
                            </p>
                        </div>

                        <div class="about_page_meta_child">
                        <img src={Nude}className="about_page_icon" />
                            <h3> Filter of Nude Image and Video content </h3>
                            <p>Upon detection of Nudity content, this Machine learning Algorithm prevents the upload of such
                                dangerous content into the internet.
                            </p>
                        </div>

                        <div class="about_page_meta_child">
                            <img src={API}className="about_page_icon" />
                            <h3> API Integration </h3>
                            <p> We are providing an Application Programming Interface (API)
                                which can be used by new and existing internet platforms that support 
                                a sane internet enviroment.

                            </p>
                        </div>
                    </div>
            </div>
        );
    }
}

export default Features;