import React, {Component} from 'react';
import Adewale from '.././images/Adewale.jpg';
import Opeyemi from '.././images/opeyemi.jpg';
import Balogun from '.././images/balogun.jpg';
import Illus from '.././illustration/undraw_Private_data_re_4eab.svg';


class Team extends Component{
    render(){
        return(
                <div class="team_widget">
                    <h3><i class="fi-rr-users before_icon"></i>Team Members </h3>

                    <div class="team_main"> 
                        <div className="team_member">
                                <img src={Adewale} alt="Adewale Daniel" />
                                <h3>Daniel Adewale <i class="fas fa-check-circle after_icon"></i></h3>
                                <p> Front end developer</p>
                        </div>
                        <div className="team_member">
                        <img src={Opeyemi} alt="Adewale Daniel" />
                                <h3>Opeyemi Adewole <i class="fas fa-check-circle after_icon"></i></h3>
                                <p> Machine Learning Engineer</p>
                        </div>
                        <div className="team_member">
                        <img src={Balogun} alt="Adewale Daniel" />
                                <h3>Precious Balogun<i class="fas fa-check-circle after_icon"></i></h3>
                                <p> Back End developer</p>
                        </div>
                    </div>
                </div>
        );
    }
}

export default Team;